/* eslint-disable */

import React, { useRef, useEffect } from 'react';
import HomeMain from '../../components/home-main';
import Space from '../../components/space';
import Sandbox from '../../components/sandbox';
import Features from '../../components/features';
import AboutUs from '../../components/about-us';
import LadyApes from '../../components/lady-apes/lady-apes';
import About from '../../components/about';
import RoadMap from '../../components/roadMap';
import Faq from '../../components/faq';
import Tokenomics from '../../components/tokenomics';
import Team from '../../components/team';
import Investors from '../../components/investors';
import Terms from '../../components/terms';
import Footer from '../../components/footer';

const showUpBtn = (upBtn) => {
  const isBtnHidden = upBtn.classList.contains('up-btn_hidden');

  if (window.pageYOffset > 2400 && isBtnHidden) {
    upBtn.classList.remove('up-btn_hidden');
  }

  if (window.pageYOffset <= 2400 && !isBtnHidden) {
    upBtn.classList.add('up-btn_hidden');
  }
};

function Home() {
  const handleUpBtn = () => {
    window.scrollTo({
      top: 0,
      behavior: 'smooth',
    });
  };

  const upBtnRef = useRef(null);

  useEffect(() => {
    const upBtn = upBtnRef.current;

    window.addEventListener('scroll', () => {
      showUpBtn(upBtn);
    });

    return () => {
      window.addEventListener('scroll', () => {
        showUpBtn(upBtn);
      });
    };
  }, []);

  return (
    <div className="home">
      <HomeMain />
      <Space />
      <Sandbox />
      <Features />
      <LadyApes />
      <About />
      <RoadMap />
      <AboutUs />
      <Investors />
      <Faq />
      <Tokenomics />
      <Team />
      <Terms />
      <Footer />
      <button type="button" ref={upBtnRef} onClick={handleUpBtn} className="up-btn up-btn_hidden">
        <svg width="16" height="28" viewBox="0 0 16 28" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path
            d="M7.29289 0.292892C7.68342 -0.0976315 8.31658 -0.0976315 8.70711 0.292892L15.0711 6.65685C15.4616 7.04738 15.4616 7.68054 15.0711 8.07107C14.6805 8.46159 14.0474 8.46159 13.6569 8.07107L8 2.41421L2.34315 8.07107C1.95262 8.46159 1.31946 8.46159 0.928932 8.07107C0.538408 7.68054 0.538408 7.04738 0.928932 6.65685L7.29289 0.292892ZM9 1V27.5H7V1H9Z"
            fill="white"
          />
        </svg>
      </button>
      <div className="back-blur" />
    </div>
  );
}

export default Home;
