import React, { useRef } from 'react';
import tokenomics from '../../assets/images/tokenomics.svg';
import tokenomics_1440 from '../../assets/images/tokenomics_1440.svg';

const Tokenomics = () => {
	const tokenomicsModalRef = useRef(null);

	const handleModalOpen = () => {
		const modal = tokenomicsModalRef.current;
		const htmlEl = document.documentElement;
		const blurBg = document.querySelector('.back-blur');

		// Check if modal already open
		if (modal.classList.contains('tokenomics-scheme_open') || htmlEl.classList.contains('is-locked')) {
			return;
		}

		blurBg.classList.add('back-blur_active');
		modal.classList.add('tokenomics-scheme_open');
		htmlEl.classList.add('is-locked');
	};

	const handleModalClose = () => {
		const modal = tokenomicsModalRef.current;
		const htmlEl = document.documentElement;
		const blurBg = document.querySelector('.back-blur');

		// Check if modal already closed
		if (!modal.classList.contains('tokenomics-scheme_open') || !htmlEl.classList.contains('is-locked')) {
			return;
		}

		blurBg.classList.remove('back-blur_active');
		modal.classList.remove('tokenomics-scheme_open');
		htmlEl.classList.remove('is-locked');
	};

	return (
		<section className="tokenomics" id="tokenomics">
			<div className="tokenomics-wrapper">
				<h1 className="title title_size-l tokenomics-title">Tokenomics</h1>
				<div className="tokenomics-scheme" ref={tokenomicsModalRef}>
					<div className="tokenomics-scheme__wrapper">
						<div className="tokenomics-scheme__close-wrapper">
							<button
								onClick={handleModalClose}
								className="tokenomics-scheme__close-btn"
								aria-label="close"
							>
								<span />
								<span />
							</button>
						</div>
						<h1 className="title tokenomics-modal__title">Tokenomics</h1>
						<img src={tokenomics} alt="tokenomics scheme" />
						<img data-img="1440" src={tokenomics_1440} alt="tokenomics scheme" />
					</div>
				</div>
				<div className="tokenomics-btn__wrapper">
					<button onClick={handleModalOpen} className="btn btn_yellow tokenomics-btn">
						Open table
					</button>
				</div>
			</div>
		</section>
	);
};

export default Tokenomics;
