import React from 'react';
import bg from '../../assets/images/BG-full.svg';
import SectionTitle from '../section-title';
import roadmap1 from '../../assets/images/roadmap1.svg';
import roadmap2 from '../../assets/images/roadmap2.svg';
import roadmap3 from '../../assets/images/roadmap3.svg';
import roadmap4 from '../../assets/images/roadmap4.svg';
import roadmap5 from '../../assets/images/roadmap5.svg';
import roadmap6 from '../../assets/images/roadmap6.svg';
import roadmap7 from '../../assets/images/roadmap7.svg';

const roadmapItems = [
	{
		id: 1,
		reversed: true,
		img: roadmap1,
		align: 'left',
		date: 'october 2021',
		subtitle: 'Merch Store Launch and Charity',
		text:
			'There are many variations of passages of Lorem Ipsum available, but the majorit have suffered alteration in some form.'
	},
	{
		id: 2,
		reversed: false,
		img: roadmap2,
		align: 'center',
		date: 'december 2021',
		subtitle: 'New NFT collection - Introducing Female Apes',
		text:
			'There are many variations of passages of Lorem Ipsum available, but the majorit have suffered alteration in some form.'
	},
	{
		id: 3,
		reversed: false,
		img: roadmap3,
		align: 'right',
		date: 'january 2022',
		subtitle: 'Public release of the Apes of Space game whitepaper for The Sandbox',
		text:
			'There are many variations of passages of Lorem Ipsum available, but the majorit have suffered alteration in some form.'
	},
	{
		id: 4,
		reversed: false,
		img: roadmap4,
		align: 'center',
		date: 'q1 2022',
		subtitle: 'Apes of Space VX launch playable voxel - based avatars for the metaverse.',
		text:
			'There are many variations of passages of Lorem Ipsum available, but the majorit have suffered alteration in some form.'
	},
	{
		id: 5,
		reversed: true,
		img: roadmap5,
		align: 'center',
		date: 'april 2022',
		subtitle: 'Beta Release of the Apes of Space Play-to-Earn game',
		text:
			'There are many variations of passages of Lorem Ipsum available, but the majorit have suffered alteration in some form.'
	},
	{
		id: 6,
		reversed: false,
		img: roadmap6,
		align: 'center',
		date: 'june 2022',
		subtitle: 'Starting a family — Introducing Baby Apes!',
		text:
			'There are many variations of passages of Lorem Ipsum available, but the majorit have suffered alteration in some form.'
	},
	{
		id: 7,
		reversed: false,
		img: roadmap7,
		align: 'left',
		date: 'august 2022',
		subtitle: 'Final Release of the Apes of Space Play-to-Earn game',
		text:
			'There are many variations of passages of Lorem Ipsum available, but the majorit have suffered alteration in some form.'
	}
];

const RoadMap = () => {
	return (
		<section className="road-map" id="roadmap" data-aos="fade-up">
			<div className="road-map__wrapper">
				<img data-img="bg" loading="lazy" src={bg} alt="" />
				<div className="road-map__dark" />
				<div className="container">
					<h1 className="title title_size-l road-map__title">Roadmap</h1>
					<div className="road-map__road-wrapper">
						{roadmapItems.map(item => {
							const flexAlignProperties = {
								left: 'flex-start',
								center: 'center',
								right: 'flex-end'
							};

							let imgOrder = item.reversed ? 1 : 0;
							let flexAlign = flexAlignProperties[item.align];
							let align = item.align;

							if (window.innerWidth <= 1200) {
								flexAlign = 'flex-start';
								align = 'left';
								imgOrder = 0;
							}

							return (
								<div
									data-item={item.id}
									className="road-map__item"
									key={item.id}
									style={{ alignItems: flexAlign, textAlign: align }}
								>
									<div className="road-map__item-img" style={{ order: imgOrder }}>
										<img src={item.img} alt="" />
									</div>
									<h4 className="title title_item-l road-map__item-title">{`${item.id}. ${item.date}`}</h4>
									<p>{item.subtitle}</p>
									<p>{item.text}</p>
								</div>
							);
						})}
						<div className="road-map__road">
							<svg
								data-img="road"
								width="1730"
								height="810"
								viewBox="0 0 1730 810"
								fill="none"
								xmlns="http://www.w3.org/2000/svg"
							>
								<path
									d="M2 2C457.952 351.399 1582.3 -231.635 1717.96 330.394C1760.84 508.052 1656.82 558.334 1586.22 608.716C1047.39 897.364 346.963 405.049 2 809"
									stroke="white"
									strokeOpacity="0.4"
									strokeWidth="3"
									strokeDasharray="10 10"
								/>
							</svg>
							<svg
								data-img="road-1440"
								width="1252"
								height="777"
								viewBox="0 0 1252 777"
								fill="none"
								xmlns="http://www.w3.org/2000/svg"
							>
								<path
									d="M2 2C331.68 337.112 1144.65 -222.081 1242.74 316.965C1273.75 487.359 1198.54 535.584 1147.48 583.906C757.878 860.751 251.429 388.568 2 776"
									stroke="white"
									strokeOpacity="0.4"
									strokeWidth="3"
									strokeDasharray="6 6"
								/>
							</svg>
							<svg
								data-img="road-1200"
								width="33"
								height="1042"
								viewBox="0 0 33 1042"
								fill="none"
								xmlns="http://www.w3.org/2000/svg"
							>
								<path
									d="M15.7443 1C9.48327 16.2632 2.04836 60.2632 22.3966 114.158C42.7448 168.053 25.9926 218.895 15.7443 251.526C2.9614 292.228 9.60589 360.696 22.0053 412.053C34.9747 465.77 32.0201 485.504 26.7838 540.203C25.9325 549.097 23.8098 557.829 21.1891 566.37C10.1782 602.257 7.59463 643.751 22.0053 688.368C36.1661 732.212 22.9282 842.579 13.3879 878.895C3.84767 915.211 -13.1965 978.684 23.4304 1041"
									stroke="white"
									strokeOpacity="0.4"
									strokeDasharray="7 7"
								/>
							</svg>
							<svg
								data-img="road-576"
								width="33"
								height="990"
								viewBox="0 0 33 990"
								fill="none"
								xmlns="http://www.w3.org/2000/svg"
							>
								<path
									d="M15.3463 1C9.11176 15.5 1.70824 57.3 21.9705 108.5C42.2328 159.7 25.5514 208 15.3463 239C2.61742 277.667 9.23386 342.712 21.5809 391.5C34.3919 442.122 31.5996 460.976 26.4644 511.998C25.5315 521.267 23.207 530.349 20.4077 539.234C9.7489 573.066 7.38971 612.082 21.581 654C35.6819 695.651 22.5 800.5 13 835C3.49995 869.5 -13.4722 929.8 23 989"
									stroke="white"
									strokeOpacity="0.4"
									strokeDasharray="7 7"
								/>
							</svg>
						</div>
					</div>
				</div>
			</div>
		</section>
	);
};

export default RoadMap;
